import React, {
    useCallback,
    useMemo,
    useRef,
    useState,
    useEffect,
} from "react";
import axios from "axios";
import { GoogleOAuthProvider, useGoogleLogin } from "@react-oauth/google";
import defaultEvents from "./defaultEvents.json";
import "@mobiscroll/react/dist/css/mobiscroll.min.css";
import {
    Button,
    Datepicker,
    Dropdown,
    Eventcalendar,
    Input,
    Popup,
    Segmented,
    SegmentedGroup,
    setOptions,
    Snackbar,
    Select,
    Switch,
    Textarea,
    Toast,
} from "@mobiscroll/react";
import "./App.css";

setOptions({
    theme: "ios",
    themeVariant: "light",
});

const now = new Date();
const colors = [
    "#ffeb3c",
    "#ff9900",
    "#f44437",
    "#ea1e63",
    "#9c26b0",
    "#3f51b5",
    "",
    "#009788",
    "#4baf4f",
    "#7e5d4e",
];

function App() {
    const [events, setEvents] = useState(defaultEvents);
    const [myEvents, setMyEvents] = useState(defaultEvents);
    const [tempEvent, setTempEvent] = useState(null);
    const [undoEvent, setUndoEvent] = useState(null);
    const [isOpen, setOpen] = useState(false);
    const [isToastOpen, setToastOpen] = useState(false);
    const [toastText, setToastText] = useState();
    const [isEdit, setEdit] = useState(false);
    const [anchor, setAnchor] = useState(null);
    const [start, startRef] = useState(null);
    const [end, endRef] = useState(null);
    const [popupEventTitle, setTitle] = useState("");
    const [popupEventSelect, setSelect] = useState("");
    const [popupEventAllDay, setAllDay] = useState(true);
    const [popupTravelTime, setTravelTime] = useState(0);
    const [popupEventDate, setDate] = useState([]);
    const [popupEventStatus, setStatus] = useState("busy");
    const [mySelectedDate, setSelectedDate] = useState(now);
    const [colorPickerOpen, setColorPickerOpen] = useState(false);
    const [colorAnchor, setColorAnchor] = useState(null);
    const [selectedColor, setSelectedColor] = useState("");
    const [color, setColor] = useState("");
    const [tempColor, setTempColor] = useState("");
    const [isSnackbarOpen, setSnackbarOpen] = useState(false);

    const [selectedPM, setSelectedPM] = useState("");
    const [selectedWorker, setSelectedWorker] = useState([]);

    const colorPicker = useRef();

    const myView = useMemo(
        () => ({
            calendar: {
                labels: true,
                popover: true,
                popoverClass: "custom-event-popover",
            },
        }),
        []
    );

    useEffect(() => {
        fetchEvents();
    }, []);

    const fetchEvents = () => {
        // defaultEvents   
        setEvents(defaultEvents);
    };

    const addEventToServer = (eventData) => {
        axios
            .post("/events", eventData)
            .then((response) => {
                console.log("  :", response.data);
                fetchEvents();
            })
            .catch((error) => {
                console.error("  :", error);
            });
    };

    const updateEventOnServer = (eventData) => {
        axios
            .put(`/events/${eventData.id}`, eventData)
            .then((response) => {
                console.log("  :", response.data);
                fetchEvents();
            })
            .catch((error) => {
                console.error("  :", error);
            });
    };

    const deleteEventFromServer = (eventId) => {
        axios
            .delete(`/events/${eventId}`)
            .then((response) => {
                console.log("  :", response.data);
                fetchEvents();
            })
            .catch((error) => {
                console.error("  :", error);
            });
    };

    const colorButtons = useMemo(
        () => [
            "cancel",
            {
                text: "Set",
                keyCode: "enter",
                handler: () => {
                    setSelectedColor(tempColor);
                    setColorPickerOpen(false);
                },
                cssClass: "mbsc-popup-button-primary",
            },
        ],
        [tempColor]
    );

    const handleToastClose = useCallback(() => {
        setToastOpen(false);
    }, []);

    const colorResponsive = useMemo(
        () => ({
            medium: {
                display: "anchored",
                touchUi: false,
                buttons: [],
            },
        }),
        []
    );

    const snackbarButton = useMemo(
        () => ({
            action: () => {
                setMyEvents((prevEvents) => [...prevEvents, undoEvent]);
            },
            text: "Undo",
        }),
        [undoEvent]
    );

    const handleSnackbarClose = useCallback(() => {
        console.log("Snackbar is closing");
        setSnackbarOpen(false);
    }, []);

    const myData = [
        { text: "", value: 1 },
        { text: "", value: 2 },
        { text: "", value: 3 },
        { text: "", value: 4 },
        { text: "", value: 5 },
        { text: "", value: 6 },
        { text: "", value: 7 },
        { text: "", value: 8 },
        { text: "", value: 9 },
    ];

    const saveEvent = useCallback(() => {
        const newEvent = {
            id: tempEvent.id,
            title: popupEventTitle,
            start: popupEventDate[0],
            end: popupEventDate[1],
            allDay: popupEventAllDay,
            bufferBefore: popupTravelTime,
            status: popupEventStatus,
            color: color,
            selectedPM: selectedPM,
            selectedWorker: selectedWorker,
        };
        if (isEdit) {
            const index = myEvents.findIndex((x) => x.id === tempEvent.id);
            const newEventList = [...myEvents];
            newEventList.splice(index, 1, newEvent);
            setMyEvents(newEventList);
        } else {
            setMyEvents([...myEvents, newEvent]);
        }
        setSelectedDate(popupEventDate[0]);
        setOpen(false);
        setSelectedPM("");
        setSelectedWorker([]);
    }, [
        isEdit,
        myEvents,
        popupEventAllDay,
        popupEventDate,
        popupEventSelect,
        popupEventStatus,
        popupEventTitle,
        popupTravelTime,
        tempEvent,
        color,
        setSelectedDate,
        setOpen,
        setMyEvents,
        selectedPM,
        selectedWorker,
    ]);

    const deleteEvent = useCallback(
        (event) => {
            setMyEvents(myEvents.filter((item) => item.id !== event.id));
            setUndoEvent(event);
            setTimeout(() => {
                setSnackbarOpen(true);
            });
        },
        [myEvents]
    );

    const loadPopupForm = useCallback((event) => {
        setTitle(event.title);
        setSelect(event.selected);
        setAllDay(event.allDay);
        setDate([event.start, event.end]);
        setStatus(event.status || "busy");
        setTravelTime(event.bufferBefore || 0);
        setColor(event.color || "");
        setTempColor(event.color || "");
        setSelectedPM(event.selectedPM || "");
        setSelectedWorker(event.selectedWorker || []);
    }, []);

    const onDeleteClick = useCallback(
        (args) => {
            deleteEvent(args.event);
            setOpen(false);
        },
        [deleteEvent]
    );

    const onSelectedDateChange = useCallback((event) => {
        setSelectedDate(event.date);
    }, []);

    const onEventClick = useCallback(
        (args) => {
            setEdit(true);
            setTempEvent({ ...args.event });
            loadPopupForm(args.event);
            setAnchor(args.domEvent.target);
            setOpen(true);
        },
        [loadPopupForm]
    );

    const onEventCreated = useCallback(() => {
        const tempId = Math.floor(Math.random() * 10000);
        setEdit(false);
        setTempEvent({
            id: tempId,
            title: "",
            allDay: true,
            selected: "",
            start: mySelectedDate,
            end: mySelectedDate,
            bufferBefore: 0,
            status: "busy",
        });
        loadPopupForm({
            id: tempId,
            title: "",
            allDay: true,
            selected: "",
            start: mySelectedDate,
            end: mySelectedDate,
            bufferBefore: 0,
            status: "busy",
        });
        setAnchor(null);
        setOpen(true);
    }, [loadPopupForm, mySelectedDate]);

    const selectColor = useCallback((event) => {
        setTempColor(event.currentTarget.getAttribute("data-value"));
    }, []);

    const openColorPicker = useCallback(
        (ev) => {
            selectColor({
                currentTarget: colorPicker.current.querySelector(
                    "div[data-value='" + color + "']"
                ),
            });
            setColorAnchor(ev.currentTarget);
            setColorPickerOpen(true);
        },
        [color, selectColor]
    );

    const onClose = useCallback(() => {
        if (!isEdit) {
            setMyEvents([...myEvents]);
        }
        setOpen(false);
        setSnackbarOpen(false);
    }, [isEdit, myEvents]);

    const onPopupClose = useCallback(() => {
        setOpen(false);
    }, []);

    const onPopupClosed = useCallback(() => {
        setAnchor(null);
    }, []);

    const { login, loggedIn } = useGoogleLogin({
        onSuccess: (tokenResponse) => {
            console.log("Login Successful:", tokenResponse);
            // Fetch user information here if needed
        },
        onError: (error) => {
            console.error("Login Failed:", error);
        },
        clientId:
            "884219707874-gojehstsc39n7j9n32vfpkvhs73rqfti.apps.googleusercontent.com", // Replace with your actual client ID
    });

    return (
        <GoogleOAuthProvider clientId="884219707874-gojehstsc39n7j9n32vfpkvhs73rqfti.apps.googleusercontent.com">
            <Button onClick={login}>Login with Google</Button>
            <Eventcalendar
                view={myView}
                data={myEvents}
                clickToCreate="double"
                dragToCreate={false}
                dragToMove={true}
                dragToResize={true}
                selectedDate={mySelectedDate}
                onSelectedDateChange={onSelectedDateChange}
                onEventClick={onEventClick}
                onEventCreated={onEventCreated}
            />
            <Popup
                display="bottom"
                fullScreen={true}
                contentPadding={false}
                headerText={isEdit ? "Edit event" : "New Event"}
                anchor={anchor}
                buttons={[
                    "cancel",
                    {
                        handler: saveEvent,
                        keyCode: "enter",
                        text: isEdit ? "Save" : "Add",
                        cssClass: "mbsc-popup-button-primary",
                    },
                ]}
                isOpen={isOpen}
                onClose={onClose}
                responsive={{
                    medium: {
                        display: "anchored",
                        width: 400,
                    },
                }}
            >
                <div className="mbsc-form-group">
                    <Input
                        label="Event title"
                        value={popupEventTitle}
                        onChange={(ev) => setTitle(ev.target.value)}
                    />
                    <Textarea
                        label="PM"
                        value={popupEventTitle}
                        onChange={(ev) => setSelectedPM(ev.target.value)}
                    />
                    <Textarea
                        label="Workers"
                        value={popupEventTitle}
                        onChange={(ev) => setSelectedWorker(ev.target.value)}
                    />
                    <Switch
                        label="All-day"
                        checked={popupEventAllDay}
                        onChange={(ev) => setAllDay(ev.target.checked)}
                    />
                    <Datepicker
                        select="range"
                        controls={["calendar"]}
                        touchUi={true}
                        startInput={start}
                        endInput={end}
                        showRangeLabels={false}
                        responsive={{
                            medium: {
                                controls: ["calendar", "time"],
                                touchUi: false,
                            },
                        }}
                        onChange={(args) => setDate(args.value)}
                        value={popupEventDate}
                    />
                    <Input
                        ref={startRef}
                        label="Starts"
                        className="md-event-start"
                    />
                    <Input ref={endRef} label="Ends" className="md-event-end" />
                    <Dropdown
                        label="Status"
                        value={popupEventStatus}
                        onChange={(ev) => setStatus(ev.target.value)}
                        data={[
                            {
                                value: "busy",
                                text: "Show as busy",
                            },
                            {
                                value: "free",
                                text: "Show as free",
                            },
                        ]}
                    />
                    <Input
                        label="Buffer Time"
                        value={popupTravelTime}
                        onChange={(ev) => setTravelTime(ev.target.value)}
                    />
                    <div onClick={openColorPicker} className="event-color-c">
                        <div className="event-color-label">Color</div>
                        <div
                            className="event-color"
                            style={{ background: selectedColor }}
                        ></div>
                    </div>
                    <Select
                        label="Select PM"
                        data={myData}
                        value={selectedPM}
                        onChange={(event) => setSelectedPM(event.target.value)}
                    />
                    <Select
                        label="Select Worker"
                        data={myData}
                        value={selectedWorker}
                        onChange={(event) =>
                            setSelectedWorker(event.target.value)
                        }
                        multiple
                    />
                </div>
            </Popup>
            <Popup
                display="bottom"
                contentPadding={false}
                isOpen={colorPickerOpen}
                anchor={colorAnchor}
                buttons={colorButtons}
                responsive={colorResponsive}
                ref={colorPicker}
                onClose={() => setColorPickerOpen(false)}
            >
                <div className="crud-color-row">
                    {colors.map((c, index) => (
                        <div
                            key={index}
                            className={
                                "crud-color-c " +
                                (c === tempColor ? "selected" : "")
                            }
                            data-value={c}
                            onClick={selectColor}
                        >
                            <span
                                className="crud-color"
                                style={{ background: c }}
                            ></span>
                        </div>
                    ))}
                </div>
            </Popup>
            <Snackbar
                display="bottom"
                button={snackbarButton}
                isOpen={isSnackbarOpen}
                onClose={handleSnackbarClose}
                onClosed={onClose}
                message={toastText}
            />
        </GoogleOAuthProvider>
    );
}

export default App;
